.importance {
    padding: 60px 20px;
    background-color: #fff;
    text-align: center;
  }
  
  .importance h2 {
    font-size: 2.5rem;
    margin-bottom: 30px;
    color: #333;
  }
  
  .importance-items {
    display: flex;
    flex-wrap: wrap; /* Allows items to wrap to the next line on smaller screens */
    justify-content: center;
    gap: 20px; /* Adds space between items */
  }
  
  .importance-item {
    background-color: #f4f4f4;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    padding: 20px;
    text-align: center;
    flex: 1 1 200px; /* Allows the items to grow and shrink, with a minimum width of 200px */
    max-width: 300px; /* Limits the maximum width of each item */
    margin-bottom: 20px; /* Adds space between rows on smaller screens */
  }
  
  .importance-item i {
    color: #ff6347;
    margin-bottom: 15px;
  }
  
  .importance-item h3 {
    font-size: 1.5rem;
    margin-bottom: 10px;
    color: #333;
  }
  
  .importance-item p {
    font-size: 1rem;
    color: #555;
  }
  
  /* Media Query for Smaller Devices */
  @media (max-width: 768px) {
    .importance-items {
      flex-direction: column; /* Stacks items vertically on smaller screens */
      align-items: center; /* Centers items within the container */
    }
  
    .importance-item {
      max-width: 90%; /* Allows items to take up most of the screen width on smaller devices */
    }
  }
  