.hero {
    /* Uncomment and replace 'hero-image.jpg' with your desired image */
    /* background: url('hero-image.jpg') no-repeat center center/cover; */
    height: 100vh;
    color: black;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    padding: 0 20px;
}

.hero .container {
    display: flex;
    flex-direction: column; /* Stack items vertically in the hero container only */
    align-items: center; /* Center align horizontally */
    justify-content: center; /* Center align vertically */
}

.hero-title {
    font-size: 3rem;
    margin-bottom: 10px; /* Adjusts spacing below the title */
    animation: fadeInDown 1s ease-in-out;
}

.hero-subtitle {
    font-size: 1.25rem;
    margin-bottom: 20px; /* Adjusts spacing below the subtitle */
    animation: fadeInUp 1s ease-in-out;
}

.btn-primary {
    background-color: #ff6347;
    color: white;
    padding: 12px 30px;
    text-decoration: none;
    border-radius: 5px;
    margin-top: 20px;
    display: inline-block;
    transition: background-color 0.3s;
}

.btn-primary:hover {
    background-color: #e5533b;
}

/* Animation Keyframes */
@keyframes fadeInDown {
    0% {
        opacity: 0;
        transform: translateY(-20px);
    }
    100% {
        opacity: 1;
        transform: translateY(0);
    }
}

@keyframes fadeInUp {
    0% {
        opacity: 0;
        transform: translateY(20px);
    }
    100% {
        opacity: 1;
        transform: translateY(0);
    }
}
