/* Header Styles */
.header {
  background-color: #333;
  color: #fff;
  padding: 15px 0;
  position: sticky;
  top: 0;
  width: 100%;
  z-index: 1000;
}

.container {
  display: flex;
  justify-content: space-between; /* Ensure space between logo and nav */
  align-items: center;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}

.logo {
  font-size: 24px;
  font-weight: bold;
  text-decoration: none;
  color: #fff;
}

/* Menu Toggle for Mobile */
.menu-toggle {
  display: none;
  font-size: 24px;
  cursor: pointer;
  color: white;
  z-index: 1010;
}

/* Desktop Navigation */
nav {
  display: flex;
}

.nav-links {
  list-style: none;
  display: flex;
  gap: 20px;
  margin: 0;
  padding: 0;
}

.nav-links li a {
  text-decoration: none;
  color: #fff;
  font-size: 16px;
  transition: color 0.3s ease;
}

.nav-links li a:hover {
  color: #ff9800;
}

/* Mobile Styles */
@media (max-width: 768px) {
  .menu-toggle {
    display: block; /* Show the menu toggle on small screens */
  }

  nav {
    position: absolute; /* Position the nav absolutely to overlay content */
    top: 60px; /* Adjust based on header height */
    right: 0; /* Align to the right */
    width: 100%; /* Full width for mobile view */
    background-color: #333; /* Match background color */
    padding: 10px 20px; /* Add padding */
    display: flex;
    flex-direction: column;
    align-items: flex-end; /* Align items to the right */
    gap: 10px;
  }

  .nav-links {
    display: none; /* Hide the navigation by default */
  }

  .nav-links.active {
    display: flex; /* Show the navigation when active */
  }
}
