.contact {
    padding: 60px 20px;
    background-color: #f4f4f4;
    text-align: center;
  }
  
  .newsletter {
    max-width: 500px;
    margin: 0 auto;
    padding: 40px;
    background-color: white;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }
  
  .newsletter h2 {
    font-size: 2.5rem;
    margin-bottom: 20px;
    color: #333;
  }
  
  .newsletter p {
    font-size: 1.2rem;
    margin-bottom: 30px;
    color: #555;
  }
  
  .newsletter form {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px; /* Space between input and button */
    flex-wrap: wrap; /* Allows wrapping on small screens */
  }
  
  .newsletter input[type="email"] {
    padding: 10px;
    font-size: 1rem;
    border: 1px solid #ddd;
    border-radius: 5px;
    width: 100%; /* Full width for responsiveness */
    max-width: 300px;
    box-sizing: border-box; /* Includes padding and border in width calculation */
    margin-bottom: 10px; /* Adds space between elements on smaller screens */
  }
  
  .btn-primary {
    background-color: #ff6347;
    color: white;
    padding: 11px 20px;
    width: 50%;
    text-decoration: none;
    border-radius: 5px;
    cursor: pointer;
    border: none;
    transition: background-color 0.3s;
    display: inline-block;
    line-height: 1; 
  }
  
  /* Media Query for Small Devices */
  @media (max-width: 500px) {
    .newsletter form {
      flex-direction: column; /* Stacks input and button vertically */
      gap: 0; /* Removes gap since elements are stacked */
    }
  
    .newsletter input[type="email"],
    .btn-primary {
      width: 100%; /* Full width on small devices */
      max-width: none; /* Removes max-width constraint */
    }
  }
  